.nav{
    background-color: #0b0a0f;
    min-height: 100vh;
    width: 40px;
    padding: 10px 40px;
    display: flex;
    z-index: 10000;
    position: fixed;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 
    rgba(255, 147, 203, 0.11) 1px -28px 29px,
    rgba(255, 147, 203, 0.158) 1px 19px 28px, 
    rgba(255, 147, 203, 0.11) 1px 27px 28px,
    rgba(255, 147, 203, 0.11) 1px -25px 28px;

}

.nav__item{
    display: flex;
    writing-mode: vertical-rl;
    transform: scale(-1);
    font-weight: 600;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    color: white;
    padding: 25px 0px;
    animation: pulsate 0.11s ease-in-out infinite alternate;   
}

.nav__item_active{
    position: relative;
    @extend .nav__item;  
    transition: .5s all;
    text-shadow:
    0 0 .7px #fff,
    0 0 3px #fff,
    0 0 21px #fff,
    0 0 42px #f09,
    0 0 82px #f09,
    0 0 92px #f09,
    0 0 102px #f09,
    0 0 151px #f09;
    animation: pulsate 0.11s ease-in-out infinite alternate;     

}
.nav__item_active::before {
    content: "";
    position: absolute;
    right: 20px;
    bottom: 50%;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: rgb(255, 255, 255);
    transition: .5s all;
  }
.nav__item:hover,
.nav__item:focus{
    transform: scale(-1.2);
    cursor: pointer;
    outline: none;
}
.nav__img{
    width: 40px;
    height: 40px;
    object-fit: contain;    
}
.nav__imgBlok{
    background-color: white;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    // margin-bottom: 50px;
    box-shadow: 
                rgba(255, 147, 203, 0.3) 0px -12px 13px,
                rgba(255, 147, 203, 0.3) 0px 4px 13px, 
                rgba(255, 147, 203, 0.3) 0px 12px 13px,
                rgba(255, 147, 203, 0.3) 0px -3px 13px;
}

@media (max-height:600px){
.nav__item{
    padding: 15px 0px;
}
}
@media (max-width:768px){
    .nav{
        box-sizing: border-box;
        min-height: 30px;
        width: 100%;
        flex-direction: row;
        padding: 15px 40px;
        justify-content: space-between;
        align-items: center;
    }
    .nav__imgBlok{
        margin-bottom: 0px;      
    }
    .nav__list{
        display: none;
    }
    .works{
        padding: 0px;
    }
    .burger{
        display: block;
        position: relative;
        width: 30px;
        height: 20px;
    }
    .burger span{
        position: absolute;
        background-color: white;
        left: 0;
        width: 100%;
        height: 2px;
        top: 0px
    }
    .burger::before,
    .burger::after{
        content: "";
        position: absolute;
        background-color: white;
        left: 0;
        width: 100%;
        height: 2px;
        top: 9px
    }
    .burger::before{
        top:18px
    }
}
