$ContainerBackground:#0b0a0fe9;
@import "../About/AboutMe.module.scss";

$backgroundBox: rgb(21, 21, 25);
@keyframes ShowBlok{
    0% {
      opacity: 0;
    }
    100% {
      opacity: 100%;
    }
  }
.contacts{
    background-color: $ContainerBackground;
    min-height: 100vh;
    padding: 0px 0px 0px 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.header{
    animation:none;
}
.container{
    margin-bottom: 50px;
    animation: ShowBlok 1s;
}

.contactItem{
    background-color: $backgroundBox;
    padding: 20px;
    box-shadow: 20px 20px 20px rgba(144, 101, 126, 0.263),
    0 0 0 rgba(244, 132, 197, 0.663), 0 0 0 rgba(244, 132, 197, 0.663) inset,
    2px 2px 2px rgba(244, 132, 197, 0.663) inset;
    display: flex;
    align-items: center;
}
.contactItem:hover{
    .contactItem_socialPic{
        transform: scale(1.1);
    }
}
.contactItem_socialPic{
    width: 45x;
    height: 45px;
}
.contactItem__header{
    color: white;
    font-size: 20px;
    padding: 0px 20px;
    width: 100px;
}

.contactItem__info{
    color: white;
    font-size: 16px;
    padding: 0px 20px;
    width: 250px;
}
.contactItem__btn{
    color: #FFB7EA;
    margin-right: 10px;
    font-size: 20px;
    padding: 0px 20px;
}
.contactItem__arrow{
    margin-left: 10px;
    transition: .3s all;
}

.contactItem__btn:hover, 
.contactItem__btn:focus{
    .contactItem__arrow{
        transform: translate(10px, 0px);
        transition: .3s all;
    }
    outline: none;
}


.neonBtn{
    margin-bottom: 50px;
    position: relative;
    width: 270px;
    animation: none;
}
.neonBtn:focus{
outline: none;
}
.neonBtn:hover::after{
        transition-delay: 1s;
        content: "";
        width: 30px;
        height: 30px;
        background: url("../../img/saveBlack.png") no-repeat;
        position: absolute;
        top: calc(50% - 15px);
        left: 280px;
    }
.neonBtn::after{
    content: "";
    width: 30px;
    height: 30px;
    background: url("../../img/save.png") no-repeat;
    position: absolute;
    top: calc(50% - 15px);
    left: 280px;
}
.save{
    margin-left: 20px;
}

@media (max-width:900px){
    .works{
        padding: 50px 0px 0px 0px;
    }
    .contactItem{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .contactItem__header,
    .contactItem__info{
        width: auto;
        margin-bottom: 20px;
    }
    
    .contactItem__btn{
        margin: 0px;
    }
    .contactItem_socialPic{
        margin-bottom: 20px;
    }
}
@media (max-width:768px){
    .contacts{
        padding: 50px 0px 0px 0px;
    }

}
@media (max-width:390px){
    .header{
        padding-left: 20px;
        padding-right: 20px;
        text-align: center;
       padding-bottom: 20px;
    }
    .contactItem__info{
        font-size: 12px;
    }
    .neonBtn{
       font-size: 16px;
        width: 200px;
        margin: 0px auto 50px;
    }
    .neonBtn::after{
        left: 220px;
    }
}
